        convertBase64UrlToBlob (base64,mimeType){
          let bytes = window.atob(base64.split(',')[1]);
          let ab = new ArrayBuffer(bytes.length);
          let ia = new Uint8Array(ab);
          for (let i =0;i< bytes.length ; i++){
            ia[i] = bytes.charCodeAt(i);
          }
          let _blob = new  Blob([ab],{type: mimeType});
          return _blob;
        },
        /*
        *  base64 压缩
        * */
        compress(base64 , quality, mimeType) {
          const MAX_WIDTH = 800;
          let cvs = document.createElement('canvas');
          let img = document.createElement('img');
          img.crossOrigin = 'anonymous';
          return new Promise(( resolve ,reject ) => {
            img.src = base64;
            // 图片 偏移值
            let offetX = 0;
            img.onload = function () {
              if (img.width > MAX_WIDTH){
                cvs.width = MAX_WIDTH ;
                cvs.height = img.height * MAX_WIDTH/img.width;
                offetX = (img.width - MAX_WIDTH) /2;
              }else {
                cvs.width = img.width;
                cvs.height = img.height;
              }
              let ctx = cvs.getContext('2d').drawImage(img,0,0,cvs.width,cvs.height);
              let imageData = cvs.toDataURL(mimeType,quality/100);
              resolve(imageData);
            }
          })
